.footer {
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(19, 19, 19, 0.8);
}

.footer-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
    max-width: 1140px;
    margin: auto;
}

.left {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    padding: 1rem;
}

.location{
    display: flex;
    margin-bottom: 0.8rem;
}

.location p {
    line-height: 30px;
}

.right {
   display: flex;
   flex-direction: column;
   height: 100%;
   max-width: 100%;
   padding: 1rem; 
}

.social {
    margin-top: 1rem;
}

.social-icon:hover {
    color: #39FF14;
}

h4 {
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

p {
   font-size: 1.2rem; 
}

@media screen and (max-width: 640px){
    .footer-container {
        grid-template-columns: 1fr;
    }
}